<template>
  <div class="container">
    <Header :showAddTask="showAddTask" @btn-click="toggleAddTask" title="Task Tracker"></Header>
    <div v-if="showAddTask">
      <AddTask @add-task = "addTask" />
    </div>
    <Tasks @delete-task="deleteTask" @reminder-change="reminderChange" :tasks="tasks" />
  </div>
</template>
<script>
import Header from './components/Header'
import Tasks from './components/Tasks'
import AddTask from './components/AddTask'
export default {
  name: 'App',
  components: {
    Header,
    Tasks,
    AddTask,
  },
  data(){
    return{
      tasks: [],
      showAddTask : true,
    }
  },
  methods: {
    deleteTask(id){
      if(confirm('Are you sure?')){
      this.tasks = this.tasks.filter((task) => task.id !== id);
      }
    }, 
    reminderChange(id){
      this.tasks.forEach((task) => task.id == id ? task.reminder = !task.reminder : task)
    },
    addTask(task){
      this.tasks = [...this.tasks, task]
    },
    toggleAddTask(){
      this.showAddTask = !this.showAddTask;
    }
  },
  created(){
    this.tasks = [
      {
        id: 1,
        text: 'Meeting ali',
        day: 'March 1st',
        reminder: true,
      },
      {
        id: 2,
        text: 'Play games',
        day: 'March 2nd',
        reminder: true,
      },
      {
        id: 3,
        text: 'Read story',
        day: 'March 3rd',
        reminder: false,
      },
    ]
  },
}
</script>

